@import "../../assets/styles/regularStyle.scss";
.nav {
    width: 100%;
    height: 4rem;
    justify-content: space-around;
    flex-direction: row-reverse;
    display: flex;
    top: 0;
    .iconContainer {
        background-color: #ffffff30;
        padding: 0.7rem;
        border-radius: 10px;
        .icon {
            width: auto;
            height: 100%;
        }
    }
    .searchContainer {
        display: grid;
        grid-template-rows: 0fr;
        transition: 1s;
        @include regularText--cardTitle-regular;

        .inputArea {
            overflow: hidden;
            background-color: transparent;
            .input {
                border-radius: 10px;
                background-color: rgba(0, 0, 0, 0.4);
                padding: 0.5rem;
                &:active ,&:focus {
                    border: white 1px solid;
                }
            }
        }
        &.open {
            background-color: transparent;

            grid-template-rows: 1fr;
        }
    }
}
